@use 'base/variables' as v;
@use 'base/mixins' as mix;

.sidebar {
    height: 0;
    opacity: 0;
    transition-property: opacity;
    transition-duration: .5s;
    background-color: rgb(0 0 0 / .95);
    overflow: hidden;

    &.mostrar {
        height: 100%;
        opacity: 1;
        padding: 2rem;
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        @include mix.tablet {
            display: none;
        }
    }

    &.ocultar {
        opacity: 0;
    }
    
    
    @include mix.tablet {
        height: 100%;
        opacity: 1;
        padding: 2rem;
        background-color: v.$indigo;
    }

    .contenedor-menu {
        display: flex;
        justify-content: space-between;
        align-items: center;
        h2 {
            color: v.$blanco;
            margin: 0;
            text-align: center;
        }

        .cerrar-menu {
            display: flex;
            justify-content: center;
            height: 2rem;
            width: 2rem;

            img {       
                &:hover {
                    cursor: pointer;
                }
            }

            @include mix.tablet {
                display: none;
            }
        }
    
    }


    .sidebar-nav {
        margin-top: 5rem;  
         
        a{
            color: v.$blanco;
            display: block;
            padding: 1rem;
            transition: background-color 300ms;
            text-transform: uppercase;
            font-weight: bold;
            font-size: 2.4rem;
            margin-bottom: 2rem;

            &:hover,
            &.activo {
                background-color: darken(v.$indigo, 20%);
                border-radius: .5rem;
            }

            @include mix.tablet {
                font-size: 1.2rem;
                margin-bottom: 1rem;

            }
        }

    }
    
    .cerrar-sesion {
        a{
            color: v.$blanco;   
        }
        @include mix.boton(v.$indigo, v.$blanco);

        @include mix.tablet {
            display: none;
        }
    }
}