@use 'base/variables' as v;
@use 'base/mixins' as mix;

.barra-mobile {
    background-color: v.$indigo;
    display: flex;
    padding: 2rem 4rem;
    align-items: center;
    justify-content: space-between;
    @include mix.tablet {
        display: none;
    }

    h1{
        color: v.$blanco;
        font-size: 4rem;
        margin: 0;
    }

    .menu {
        img {
            width: .8rem;
            &:hover {
                cursor: pointer;
            }
        }
    }
}

.barra {
    display: none;

    @include mix.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;    
        background-color: v.$blanco;   
        padding: 1rem 2rem;
        box-shadow: 1px 12px 9px -13px rgba(0,0,0, .4); 
        
    }

    p{
        margin: 0;
        font-size: 1.4rem;

        span{
            font-weight: 700;
            color: v.$indigo;
        }
    }

    .cerrar-sesion {
        @include mix.boton(darken(v.$indigo,20%), v.$blanco);
        padding: 1rem;
        font-size: 1.2rem;
        border-radius: .5rem;
    }
}